<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<div class="c-bg"></div>
				<Map :params="mapParams" class="map" @change='change'></Map>
      </div>
      <div class="w-right">
        <div class="r-top">
          <div class="t-left">
            <ctitle :title="'信访行为转化漏斗'"></ctitle>
            <div class="echarts">
              <barline :params="xfxwBarParams" @canvasClick='canvasClick'></barline>
            </div>
          </div>
          <div class="t-right">
            <ctitle :title="'年度三率'"></ctitle>
            <div class="echarts">
              <radar class="chart" :params="zyzbRadarParams"></radar>
            </div>
          </div>
        </div>
        <div class="r-center">
          <p class="l-center"><b>{{obj.name}}</b> 转化为 <b>{{obj.name1}}</b> 转化率为 <b>{{obj.value}}%</b>，实施 <b>{{msg}}</b></p>
          <div class="l-bottom">
            <p>预警说明：</p>
            <p><span v-for="(value, key) in yjsmList" :key="key">{{key}}：{{value}}</span></p>
          </div>
        </div>
        <div class="r-bottom">
          <ctitle :title="'信访人列表'"></ctitle>
          <div class="list">
            <!-- <div class="btn">预警人员名称</div> -->
            <ul>
              <li v-for="(item, index) in xfrList" 
              @click="changeXfr(index)"
              :class="index === defaultXfr ? 'colorful': ''"
              :key="index">{{item.name}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import Map from "@/components/map/Mapyn";
  import radar from "@/components/radar/radar";
  import barline from "@/components/bar/BarLine";
  export default {
    name: 'ycxs',
    components: {
      ctitle,
      Map,
      radar,
      barline
    },
    data() {
      return {
        shengj: '云南省',
        shij: 'invalid',
        obj: {
          name: '',
          name1: '',
          value: 0
        },
        yjsmList: {
          "红色预警": "转化率超过百分之十二",
          "橙色预警": "转化率超过百分之五",
          "蓝色预警": "转化率超过百分之三",
        },
        xfrList: [],
        defaultXfr: 0,
        // 地图
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            tooltipNotShow: true,
            borderColor: '#77608e',
            scatterColor: '#3cfff8',
            emphasisColor: '#ffd200',
            normalAreaColor: '#649dfb',
            emphasisAreaColor: '#66e8ff',
            emphasisBorderColor: '#fff',
            emphasis: {
              borderWidth: 2,
              shadowColor: '#1f4ea8',
              shadowBlur: 15,
              fontSize: 16
            },
            isHome: false,
            JSON: "530000",
            xName: "name",
						yName: "value",
						top: 0,
            map: {
              "value": "信访量"
            },
            carousel: false
          }
        },
        //2.年度三率指数
        zyzbRadarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            center: ['45%', '60%'],
            radius: '65%'
          }
        },
        // 3.信访行为转化漏斗
        xfxwBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            componentType: 'series',
            axisLabelSize: 14,
            tooltipShow: false,
            xName: "name", //x轴字段
            axisValueShow: false,
            splitLineShow: false, //value轴splitline是否显示
            axisLineShow: true,
            axis1Name:"",
            axisLabelSize: "12%", // label字号
            left: "0%",
            right: "10%",
            top: "5%",
            bottom: "1%",
            barBorderRadius: 5,
            barWidth: 30,
            categoryBr: true,
            brNum: 4,
            // barLabelOptions: "top",
            barLabelShow: false, //柱状图数量值显示
            itemLabelColor: "#cdefff",
            colors: {
              bar: [[ "#01fdcc", "#11a0d8"]]
            },
            map: {
              value: "次数"
            }
          }
        },
      }
    },
    mounted() {
      this.getMapAPI()
    },
    methods: {
      change(name) {
        this.shij = name;
        this.getAPI(this.year)
      },
      changMenu(item) {
        this.selectName = item;
      },
      changeXfr(index) {
        this.defaultXfr = index;
      },
      canvasClick(item) {
        let {dataIndex, value, name} = item.params;
        let colorful={color:'#fefa82'}
        if (dataIndex) {
          this.obj = {
            name1: name,
            value: value.toFixed(2) * 100,
            name: this.xfxwBarParams.data[dataIndex-1].name
          }
        } else {
          this.obj = {
            name: name,
            value: value.toFixed(2) * 100,
            name1: this.xfxwBarParams.data[dataIndex+1].name
          }
        }
        // this.xfxwBarParams.data.forEach((item,index)=>{
        //   if(dataIndex === index){
        //     this.$set(item, "itemStyle", colorful)
        //   }else{
        //     this.$delete(item,"itemStyle");
        //   }
        // })
      },
      getMapAPI() {
        // 1.地图
        get_http("xfxwfxyjXwzhyjMap", {
          shengj: this.shengj
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
        })
        .then(res => {
          this.mapParams.data = res.data.data;
          // this.mapFlag = true;
        })
        .catch(err => {
          console.log(err);
        });
        this.getAPI(this.year)
      },
      getAPI(year) {
        // 1.信访行为转化漏斗
        get_http("xfxwfxyjXwzhyjXfxwzhld", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
          .then(res => {
            this.xfxwBarParams.data = res.data.data;
            this.obj = {
              name: this.xfxwBarParams.data[0].name,
              value: this.xfxwBarParams.data[0].value.toFixed(2) * 100,
              name1: this.xfxwBarParams.data[1].name
            }
          })
          .catch(err => {
            console.log(err);
          });
        // 2.年度三率指数
        get_http("xfxwfxyjXwzhyjNdslzs", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
        .then(res => {
          this.zyzbRadarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 3.信访人列表
        get_http("xfxwfxyjXwzhyjXfrlb", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
        .then(res => {
          this.xfrList = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      },
      msg() {
        return this.obj.value > 12 ? '红色预警' : this.obj.value > 5 ? '橙色预警' : this.obj.value > 3 ? '蓝色预警': ''
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      position relative
      flex 1
      height 100%
      display flex
      flex-direction column
      .map-chart
        background none
        position absolute
        top -45px
      .c-bg
        position absolute
        width 100%
        height 100%
        z-index 0
        top 0
        left 0
        background url("../../../assets/map-bg.png") no-repeat center bottom
    .w-right
      flex 1
      display flex
      flex-direction column
      .r-center
        height 1.4rem
        margin-bottom 15px
        .l-center
          padding 0 30px
          height 60px
          width fit-content
          background url("../img/zhld-bg.png") no-repeat
          background-size 100% 100%
          font-size 18px
          color #fff
          text-align center
          line-height 60px
          margin-left 0.2rem
          b
            color #ff5cc3
        .red
          b
            color #ff5cc3
        .orange
          b
            color #e69832
        .blue
          b 
            color #2390ed
        .l-bottom
          font-size 12px
          color #91fdff
          height 70px
          display flex
          flex-direction column
          justify-content center
          margin-left 0.2rem
          margin-top 0.1rem
          p 
            margin 5px 0
            span
              margin-right 10px
      .r-top
        flex 1
        display flex
        .t-left
          flex 1.5
          display flex
          flex-direction column
          .echarts
            flex 1
        .t-right
          flex 1
          display flex
          flex-direction column
          .echarts
            flex 1
      .r-bottom
        display flex
        flex-direction column
        flex 1.2
        .list
          margin 0 auto
          flex 1
          background url("../img/xfrlb-bg.png") no-repeat center bottom 
          position relative
          width 7.8rem
          display flex
          align-items flex-end
          margin-bottom 0.2rem
          margin-left 63px
          .btn
            width 1.76rem
            border 1px solid #29adff
            color #fff
            font-weight 700
            text-shadow 0 0 10px #fff
            border-radius 0.3rem
            background-color rgba(0, 0, 0, 0.502)
            line-height 0.3rem
            text-align center
            height 0.32rem
            font-size 0.16rem
            cursor pointer
            position absolute
            top -0.25rem
            right 1.5%
            &:after
              content ''
              background-color rgb(243, 255, 58)
              position absolute
              width 70%
              height 0.03rem
              top 100%
              left 15%
          ul
            display flex
            flex-wrap wrap
            text-align center
            margin-bottom 0.3rem
            margin-left 0.25rem
            li
              width 16%
              color #9aedfb
              font-size 16px
              font-weight bold
              line-height 34px
              cursor pointer
              margin-bottom 10px
              margin-left 20px
              border-radius 5px
            .colorful
              background url('../img/border.png') no-repeat rgba(255, 156, 0, 0.1)
              background-size contain
              color #fff
              font-weight 700
              text-shadow 0 0 10px #fff
</style>
